@import "../../css-config/mixins.scss";

.category_main_content{
    margin: 30px 0;
    @include md{
        margin: 75px 0;
    }
    .layout{
        max-width: calc(100% - 20px);
        margin: 0 auto;
        @include md{
            max-width: 1200px;
        }
    }
        .category_title{
            font-size: 24px;
            @include md{
                font-size: 34px;
            }
        }
    .product_container{
        margin: 20px 0;
        @include md{
            margin: 40px 0;
        }
    }
}