.main_cart_product{
    flex-grow: 1;

    .cart_product{
        padding: 20px 15px;
        display: flex;
        gap: 10px;

        .img_container{
            background: rgba(0, 0, 0, 0.05);
            width: 70px;
            height: 70px;
            flex-shrink: 0;

            img{
                width: 100%;
                height: 100%;
            }
        }

        .pro_details{
            overflow: hidden;
            position: relative;

            .name{
                text-overflow: ellipsis;
                white-space: nowrap;
                overflow: hidden;
                font-size: 16px;
                line-height: 1;
                margin-bottom: 10px;
                font-weight: 600;
                display: block;
                padding-right: 25px;
            }
                .txt{
                    display: flex;
                    align-items: center;
                    gap: 5px;
                    font-size: 12px;
                    font-weight: 600;
                    .highlight{
                        color: #8e2de2;
                    }
                }
                svg{
                    position: absolute;
                    top: 0;
                    right: 0;
                    cursor: pointer;
                }

                .quantity_btns{
                    width: fit-content;
                    display: flex;
                    border: 1px solid rgba(0, 0, 0, 0.2);
                    height: 30px;
                    margin-bottom: 8px;
                    span{
                        font-size: 16px;
                        display: flex;
                        width: 30px;
                        align-items: center;
                        justify-content: center;
                        cursor: pointer;
                        color: #6b6b6b;
                        &:nth-child(1){
                            font-size: 20px;
                            border-right: 1px solid rgba(0, 0, 0, 0.2);
                        }
                        &:nth-child(2){
                           width: 45px;
                        }
                        &:nth-child(3){
                            font-size: 20px;
                            border-left: 1px solid rgba(0, 0, 0, 0.2);
                        }
                    }
                }
        }

        &:hover{
            background:rgba(0, 0, 0, 0.05) ;
            transition: all 0.4s;
        }
    }
}