@import "../../../css-config/mixins.scss";

.search_modal {
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 999;
  background: #fff;
  transform: translateY(100%);
  animation: slideSearchWindow 0.5s ease forwards;

  .form_field {
    width: 100%;
    display: flex;
    justify-content: center;
    padding: 10px 50px;
    border-bottom: 1px solid rgba(0, 0, 0, 0.1);
    position: relative;
    @include md {
      padding: 20px 0;
    }
    input {
      width: 100%;
      max-width: 1200px;
      height: 50px;
      text-align: center;
      font-size: 20px;
      font-weight: 600;
      color: #212121;
      outline: none;
      border: none;
      text-transform: uppercase;
      @include md {
        font-size: 40px;
        height: 80px;
      }
    }

    .close_btn {
      position: absolute;
      font-size: 30px;
      right: 50px;
      top: 45%;
      transform: translateY(-50%);
      cursor: pointer;
      @include md {
      }
    }
  }

  .search_result_content {
    max-width: calc(100% - 20px);
    margin: 0 auto;
    @include md {
      max-width: 800px;
    }

    .search_result {
      height: calc(100vh - 110px);
      overflow: auto;
      margin: 20px 0;
      @include md {
        height: calc(100vh - 160px);
      }

      .search_result_item {
        display: flex;
        align-items: center;
        gap: 10px;
        padding: 10px 0;
        border-bottom: 1px solid rgba(0, 0, 0, 0.1);
        cursor: pointer;

        .img_container {
          background: rgba(0, 0, 0, 0.05);
          width: 60px;
          height: 60px;
          flex-shrink: 0;
          img {
            width: 100%;
            height: 100%;
          }
        }

        .pro_details {
          overflow: hidden;

          .name {
            text-overflow: ellipsis;
            white-space: nowrap;
            overflow: hidden;
            font-size: 16px;
            line-height: 1;
            margin-bottom: 10px;
            font-weight: 600;
            display: block;
          }
          .desc{
            font-size: 14px;
            line-height: 1;
            display: block;
            text-overflow: ellipsis;
            white-space: nowrap;
            overflow: hidden;
            color: rgba(0, 0, 0, 0.5);
          }
        }
      }
    }
  }
}

@keyframes slideSearchWindow {
  0% {
    transform: translateY(100%);
  }
  100% {
    transform: translateY(0);
  }
}
